import React, { ChangeEvent, useCallback, useState } from 'react';
import Label from '../components/Label';
import debounce from 'lodash/debounce';
import { isDarwin } from '@/utils/util';
import { usePreference } from '@/stores/preferences';

const globalHotkeyPrefix = 'CmdOrCtrl+Shift+';

export default function Shortcuts() {

    return (
        <>
            <h1 className="markup-h1 mt-4">Shortcuts</h1>

            <div className="grid gap-2">
                <Label htmlFor="toggleVisibilityGlobalHotkey">Toggle visibility from anywhere</Label>
                <HotkeyInput id="toggleVisibilityGlobalHotkey" />
            </div>

            <div className="grid gap-2">
                <Label htmlFor="newScreenGlobalHotkey">Clear Ray from anywhere</Label>
                <HotkeyInput id="newScreenGlobalHotkey" />
            </div>

            <div className="grid gap-2">
                <Label htmlFor="toggleAlwaysOnTopHotkey">Toggle always on top</Label>
                <HotkeyInput id="toggleAlwaysOnTopHotkey" />
            </div>
        </>
    );
}
type HotKeyPreference = 'toggleVisibilityGlobalHotkey' | 'newScreenGlobalHotkey' | 'toggleAlwaysOnTopHotkey';
function HotkeyInput({ id }: { id: HotKeyPreference; }) {
    const [valueWithPrefix, setValue] = usePreference(id);
    const value = valueWithPrefix.replace(globalHotkeyPrefix, '');
    const [_value, _setValue] = useState(value);

    const debouncedOnChange = useCallback(
        debounce((debouncedValue) => {
            if (value === debouncedValue) return;

            setValue(debouncedValue ? `${globalHotkeyPrefix}${debouncedValue}` : '');
        }, 250),
        [value]
    );

    function onChange(e: ChangeEvent<HTMLInputElement>) {
        _setValue(e.target.value);
        debouncedOnChange(e.target.value);
    }

    return (
        <div
            className="
                flex items-center w-full px-4 h-10
                bg-gray-100 outline-none rounded-sm
                text-gray-700 focus:text-indigo-700 focus:bg-indigo-50
            "
        >
            <label htmlFor={id} className="flex-none text-sm text-gray-500 select-none cursor-not-allowed pr-1">
                {isDarwin ? 'Command' : 'Control'} + Shift +{' '}
            </label>
            <input
                id={id}
                value={_value}
                className="outline-none bg-transparent flex-grow pb-0.5 uppercase"
                type="text"
                maxLength={1}
                onChange={onChange}
            />
        </div>
    );
}
