import Select from 'react-select';

type Option = {
	label: string;
	value: string | boolean | number;
}

type Props<O extends Array<Option>> = {
	options: O;
	value?: O[number] | null;
	onChange: (option?: O[number] | null) => void;
	isSearchable?: boolean;
}

export default function StyledSelect<O extends Array<Option>>(props: Props<O>) {
	return (
		<Select
			styles={styles}
			{...props}
		/>
	);
}

const styles = {
	control: (provided: {}, state: { isFocused: boolean } ) => ({
		...provided,
		width: '100%',
		padding: '0',
		minHeight: '2.5rem',
		borderWidth: 0,
		borderRadius: '0.125rem',
		boxShadow: 'none',
		backgroundColor: state.isFocused ? 'rgba(238, 242, 255, 1)' : 'rgba(243, 244, 246, 1)',
		color: state.isFocused ? 'rgba(67, 56, 202, 1)' : 'rgba(55, 65, 81, 1)',
	}),
	valueContainer: (provided: {}) => ({
		...provided,
		padding: '0 1rem',
	}),
	singleValue: (provided: {}) => ({
		...provided,
		color: 'inherit',
	}),
	indicatorSeparator: (provided: {}) => ({
		...provided,
		display: 'none',
	}),
	dropdownIndicator: (provided: {}, state: { isFocused: boolean } ) => ({
		...provided,
		marginRight: '3px',
		width: '32px',
		color: state.isFocused ? 'rgba(67, 56, 202, 1)' : 'rgba(107, 114, 128, 1)',
	}),
	menu: (provided: {}) => ({
		...provided,
		backgroundColor: 'rgba(238, 242, 255, 1)',
		boxShadow: ' 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
		borderWidth: 1,
		borderColor: 'rgba(243, 244, 246, 1)',
		marginTop: '3px',
	}),
	option: (provided: {}, state: { isSelected: boolean; isFocused: boolean }) => ({
		...provided,
		backgroundColor: state.isSelected
			? 'rgba(67, 56, 202, 1)'
			: state.isFocused
				? 'rgb(183, 201, 255, 1)'
				: 'rgba(238, 242, 255, 1)',
		color: state.isSelected ? 'rgba(255, 255, 255, 1)' : 'rgba(55, 65, 81, 1)',
		'&:hover': {
			color: state.isSelected ? 'rgba(255, 255, 255, 1)' : 'rgba(67, 56, 202, 1)',
			backgroundColor: state.isSelected ? 'rgba(67, 56, 202, 1)' : 'rgba(67, 56, 202, 0.05)',
		},
	}),
};

