import React from 'react';

import { editorOptions } from '/shared/utils';
import { usePreferencesState } from '@/stores/preferences';
import { isDarwin } from '@/utils/util';

import Select from '../components/Select';
import Input from '../components/Input';
import Label from '../components/Label';

import { AppVisibilityOption, ColorblindMode, Editor, NewWindowOption, Theme } from '/shared/types';

const themeOptions: Array<{ label: string; value: Theme }> = [
    { label: 'Follow OS preference', value: 'automatic' },
    { label: 'Light', value: 'light' },
    { label: 'Dark', value: 'dark' },
];

const colorblindOptions: Array<{ label: string; value: ColorblindMode }> = [
    { label: 'Enabled', value: 'Enabled' },
    { label: 'Disabled', value: 'Disabled' },
];

const newWindowOptions: Array<{ label: string; value: NewWindowOption }> = [
    { label: 'For every server', value: 'server' },
    { label: 'For every project', value: 'project' },
    { label: 'Never', value: 'never' },
];

const appVisibilityOptions: Array<{ label: string; value: AppVisibilityOption }> = [
    { label: isDarwin ? 'On menubar' : 'On system tray', value: 'menubar' },
    { label: isDarwin ? 'On both dock and menubar' : 'On both task bar and system tray', value: 'both' },
];

const floatingOptions: Array<{ label: string; value: boolean }> = [
    { label: 'Yes', value: true },
    { label: 'No', value: false },
];

const displayTimesOptions: Array<{ label: string; value: boolean }> = [
    { label: 'Hide', value: false },
    { label: 'Show', value: true },
];

const scrollOnLogOptions: Array<{ label: string; value: boolean }> = [
    { label: 'Yes', value: true },
    { label: 'No', value: false },
];

export default function Misc() {
    const [preferences, setPreference] = usePreferencesState();

    return (
        <>
            <h1 className="markup-h1">Behaviour</h1>
            <div className="grid gap-2">
                <Label>Open new window</Label>
                <Select
                    options={newWindowOptions}
                    value={newWindowOptions.find((o) => o.value === preferences.newWindow)}
                    onChange={o => {
                        setPreference('newWindow', o?.value!);
                    }}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>Show app</Label>
                <Select
                    options={appVisibilityOptions}
                    value={appVisibilityOptions.find((o) => o.value === preferences.appVisibility)}
                    onChange={(o) => setPreference('appVisibility', o?.value!)}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>Always on top</Label>
                <Select
                    options={floatingOptions}
                    value={floatingOptions.find((o) => o.value === preferences.floating)}
                    onChange={(o) => setPreference('floating', o?.value!)}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>History limit</Label>
                <Input
                    type="number"
                    value={preferences.historyLimit}
                    onChange={(limit) => setPreference('historyLimit', +limit)}
                />
            </div>

            <div className="grid gap-2">
                <Label>Editor</Label>
                <Select
                    options={Object.keys(editorOptions).map((editor) => ({ label: editor, value: editor as Editor }))}
                    value={
                        preferences.editor
                            ? { label: preferences.editor, value: preferences.editor }
                            : null
                    }
                    onChange={(o) => setPreference('editor', o?.value)}
                />
            </div>

            <div
                className="overflow-hidden max-h-0 aria-expanded:max-h-10 transition-[max-height] transition-200"
                aria-expanded={preferences.editor === 'Custom URL'}
            >
                <Input
                    placeholder="%path %line"
                    value={preferences.customEditor}
                    onChange={(o) => setPreference('customEditor', o)}
                />
            </div>

            <div className="ml-auto text-gray-400 text-sm select-none text-right">
                {preferences.editor === 'Custom URL' && (
                    <p className="mb-1">'%path' and '%line' will be replaced in your URL</p>
                )}
                <p>
                    Some editors might need{' '}
                    <a
                        href="https://spatie.be/docs/ray/v1/environment-specific-configuration/windows-10#phpstorm"
                        className="hover:text-gray-300 underline"
                    >
                        a few extra steps
                    </a>{' '}
                    to get this working.
                </p>
            </div>

            <h1 className="markup-h1 mt-4">UI</h1>

            <div className="grid gap-2">
                <Label>Theme</Label>
                <Select
                    options={themeOptions}
                    value={themeOptions.find((o) => o.value === preferences.theme)}
                    onChange={(o) => setPreference('theme', o?.value!)}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>Colorblind mode</Label>
                <Select
                    options={colorblindOptions}
                    value={colorblindOptions.find((o) => o.value === preferences.colorblindMode)}
                    onChange={(o) => setPreference('colorblindMode', o?.value!)}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>Scroll on new log</Label>
                <Select
                    options={scrollOnLogOptions}
                    value={scrollOnLogOptions.find((o) => o.value === preferences.scrollOnLog)}
                    onChange={(o) => setPreference('scrollOnLog', o?.value!)}
                    isSearchable={false}
                />
            </div>

            <div className="grid gap-2">
                <Label>Item timestamp</Label>
                <Select
                    options={displayTimesOptions}
                    value={displayTimesOptions.find((o) => o.value === preferences.displayTimes)}
                    onChange={(o) => setPreference('displayTimes', o?.value!)}
                    isSearchable={false}
                />
            </div>
        </>
    );
}
