import debounce from 'lodash/debounce';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import Input from '../components/Input';
import Label from '../components/Label';
import { usePreference } from '@/stores/preferences';

export default function Communication() {
    const [port, setPort] = usePreference('port');
    const [inputPort, setInputPort] = useState(port);
    const initialPort = useRef(port);

    useEffect(() => {
        initialPort.current = port;
        setInputPort(port);

    }, [port]);

    const debouncedSetPort = useCallback(debounce((value) => {
        setPort(value);
    }, 500), []);

    useEffect(() => {
        if (initialPort.current === inputPort) return;

        initialPort.current = inputPort;
        debouncedSetPort(inputPort);

    }, [inputPort]);

    return (
        <>
            <h1 className="markup-h1 mt-4">Communication</h1>
            <div className="grid gap-2">
                <Label htmlFor="port">Port</Label>
                <Input id="port" value={inputPort} type="number" min={1} max={65535} onChange={setInputPort} />
            </div>
        </>
    );
}
