import React from 'react';
import Input from '../components/Input';
import Label from '../components/Label';
import useLicense from '@/stores/license';
import ExternalLink from '@/components/ui/ExternalLink';

export default function License() {
    const license = useLicense();

    return (
        <>
            <div className="mt-4 md:flex items-center justify-between">
                <h1 className="markup-h1">License information</h1>
                {license.isValid && (
                    <div className="space-x-4 text-sm">
                        <ExternalLink href="https://spatie.be/products/ray">
                            Manage your licenses
                        </ExternalLink>

                        <button
                            className="text-indigo-500 hover:text-indigo-600 underline cursor-pointer"
                            onClick={license.decouple}
                        >
                            Decouple license
                        </button>
                    </div>
                )}
            </div>

            {license.isValid ? (
                <>
                    <div className="grid gap-2">
                        <Label htmlFor="license-key">Licensed to</Label>
                        <Input id="licensed-to" placeholder={license.licensed_to} disabled readOnly />
                    </div>
                    <div className="grid gap-2">
                        <Label htmlFor="expires-at">Expires on</Label>
                        <Input id="expires-at" placeholder={license.expiry_date} disabled readOnly />
                    </div>
                </>
            ) : (
                <div>
                    <p>
                        This application is in trial mode.{' '}
                        <button
                            className="text-indigo-500 hover:text-indigo-600 underline"
                            onClick={license.openWindow}
                        >
                            Learn more…
                        </button>
                    </p>
                </div>
            )}
        </>
    );
}
